import { Dispatch, SetStateAction, useEffect, useMemo, useState } from "react"
import { CqsItem } from "@rp/common/api-types"
import { WATCHLIST_FINANCIAL_DATA_FEATURE_NAME } from "@rp/common/settings"
import { INLINE_ATTRIBUTION_REGEX_FOR_REMOVAL } from "@rp/common/utils"
import { watchlistCustomerIo } from "@rp/react/common/customer-io"
import { useFinancialData } from "@rp/react/tanstack-api/financial-data"
import { useLocalStorage } from "@rp/react/web/common/hooks"
import { useModal } from "@rp/react/web/common/providers"
import { Box, TablePagination } from "@rp/react/web/mui/core"
import { ArrowUpward } from "@rp/react/web/mui/icons"
import {
  ColumnDef,
  PaginationState,
  flexRender,
  getCoreRowModel,
  getFilteredRowModel,
  getPaginationRowModel,
  getSortedRowModel,
  useReactTable,
} from "@tanstack/react-table"
import { SmartGridColumn } from "../WatchlistSmartGrid/WatchlistSmartGrid"
import { WatchlistSmartGridActions } from "../WatchlistSmartGrid/WatchlistSmartGridActions"
import { WatchlistSmartGridHeader } from "../WatchlistSmartGrid/WatchlistSmartGridHeader"
import { FinanceDataTabs } from "../hooks/useWatchlistsFinanceDataTabNavigation"
import { useCustomColumns } from "./CustomDataView/hooks/useCustomColumns"
import { WatchlistDataTableFakeColumnHeaderCell } from "./WatchlistDataTableCells/WatchlistDataTableFakeColumnHeaderCell"
import { WatchlistDataTableSmartGridCell } from "./WatchlistDataTableCells/WatchlistDataTableSmartGridCell"
import { WatchlistDataTabsAddColumnsTutorial } from "./WatchlistDataTabsAddColumnsTutorial"
import {
  WATCHLIST_DATA_TABS_ADD_COLUMNS_BUTTON_ID,
  WatchlistDataTabsColumnsMenu,
} from "./WatchlistDataTabsColumnsMenu"
import { WatchlistDataTabsDownloadCsv } from "./WatchlistDataTabsDownloadCsv"
import { WatchlistDataTabsTableEntity } from "./WatchlistDataTabsTable.types"
import {
  FINANCE_TABLE_BODY_CLASS,
  FINANCE_TABLE_CELL_CLASS,
  FINANCE_TABLE_HEAD_CLASS,
  FINANCE_TABLE_ITEM_PREFIX,
  FINANCE_TABLE_ROW_CLASS,
  FINANCE_TABLE_SCROLLED_HORIZONTALLY_CLASS,
  WatchlistDataTabsTableContainer,
} from "./WatchlistDataTabsTableContainer"
import { WatchlistDataTabsTableHeaderMenu } from "./WatchlistDataTabsTableHeaderMenu"
import { WatchlistDataTabsTableUnlockBanner } from "./WatchlistDataTabsTableUnlockBanner"
import { WatchlistTableLoading } from "./WatchlistTableLoading"
import { WatchlistTablePaginationActions } from "./WatchlistTablePaginationActions"
import { downloadTableCSV } from "./downloadTableCsv"
import { watchlistDataTables } from "./tables"
import { useWatchlistDataTablesOrder } from "./useWatchlistDataTablesOrder"
import { useWatchlistDataTablesSorting } from "./useWatchlistDataTablesSorting"
import { useWatchlistDataTabsVisibility } from "./useWatchlistDataTablesVisibility"

export type WatchlistDataTabsTableProps = {
  removeEntity: (id: string) => void
  canEdit: boolean
  isLoading: boolean
  entities: Array<CqsItem>
  onRowClick?: (item: CqsItem) => void
  onChatWithCell: WatchlistDataTabsTableEntity["onChatWithCell"]
  selectedEntityId?: string
  entityMotivation?: string | null
  dataTab: FinanceDataTabs
  watchlistName: string
  smartGridColumns: Array<SmartGridColumn>
  setSmartGridColumns: Dispatch<SetStateAction<Array<SmartGridColumn>>>
  setSelectedColumn: Dispatch<SetStateAction<SmartGridColumn | null | undefined>>
  financialData: ReturnType<typeof useFinancialData>
  isLoadingEntities: boolean
  isFeatureBlocked: boolean
  allEntitiesCounts: number
}

const DEFAULT_PAGE_SIZE = 10
const ROWS_PER_PAGINATION = [10, 25, 50, 100]

const stickedColumns: Record<string, number> = {
  name: 123,
  ticker: 3,
}

function stickyStyle(id: string) {
  return {
    zIndex: stickedColumns[id] !== undefined ? 1 : 0,
    left: stickedColumns[id],
    position: stickedColumns[id] !== undefined ? ("sticky" as const) : undefined,
  }
}

function WatchlistDataTabsTable({
  removeEntity,
  canEdit,
  dataTab,
  isLoading,
  selectedEntityId,
  entities,
  entityMotivation,
  watchlistName,
  smartGridColumns,
  financialData,
  isLoadingEntities,
  isFeatureBlocked,
  allEntitiesCounts,
  setSmartGridColumns,
  onRowClick,
  onChatWithCell,
  setSelectedColumn,
}: WatchlistDataTabsTableProps) {
  const {
    planComparisonModal: { handleOpen: planComparisonHandleOpen },
  } = useModal()
  const { columns, defaultSort, defaultOrder, defaultHidden } = watchlistDataTables[dataTab]
  const [sorting, setSorting] = useWatchlistDataTablesSorting(dataTab, defaultSort)
  const [columnOrder, setColumnOrder] = useWatchlistDataTablesOrder(dataTab, defaultOrder)
  const [columnVisibility, setColumnVisibility] = useWatchlistDataTabsVisibility(
    dataTab,
    defaultHidden,
  )

  // Store userPageSize in localStorage
  const [userPageSize, setUserPageSize] = useLocalStorage<number>("WFT_pageSize", DEFAULT_PAGE_SIZE)
  const [pagination, setPagination] = useState<PaginationState>({
    pageIndex: 0,
    pageSize: userPageSize,
  })

  // Update userPageSize when pagination changes
  useEffect(() => {
    if (userPageSize !== pagination.pageSize) {
      setUserPageSize(pagination.pageSize)
    }
  }, [pagination, setUserPageSize, userPageSize])

  useEffect(() => {
    setPagination(prev => {
      if (entities.length <= prev.pageSize * prev.pageIndex) {
        return {
          pageIndex: 0,
          pageSize: prev.pageSize,
        }
      }
      return prev
    })
  }, [entities.length])

  // Memoize the mapped table data to prevent unnecessary re-renders
  const tableData: Array<WatchlistDataTabsTableEntity> = useMemo(() => {
    return entities.map(entityInfo => {
      return {
        id: entityInfo.key,
        name: entityInfo.name,
        ticker: entityInfo.metadata1 ?? "",
        entityInfo,
        removeEntity,
        onChatWithCell,
        canEdit,
        isLoadingEntities,
        entityMotivation,
        financialData: financialData?.[entityInfo.key],
        onMotivationClicked: row => {
          onChatWithCell?.({
            prompt:
              row.entityMotivation
                ?.replace("{{company}}", row.entityInfo.name)
                .replace("{{watchlist}}", watchlistName ?? "") ?? "",
            value: null,
            entityInfo: row.entityInfo,
            columnName: "motivation",
          })
        },
      }
    })
  }, [
    entities,
    removeEntity,
    onChatWithCell,
    canEdit,
    isLoadingEntities,
    entityMotivation,
    financialData,
    watchlistName,
  ])

  const isLoadingFinanceData = Object.keys(financialData)
    .flatMap(item => Object.values(financialData[item]))
    .some(item => item.isLoading)

  const smartColumns = useMemo(() => {
    if (dataTab !== "smartGrid") return []
    return smartGridColumns.map(column => {
      const ret: ColumnDef<WatchlistDataTabsTableEntity> = {
        id: `smart-grid-column-${column.id}`,
        accessorFn: info => column.rows?.[info.entityInfo.key]?.value,
        size: 600,
        header: () => (
          <WatchlistSmartGridHeader column={column} setSelectedColumn={setSelectedColumn} />
        ),
        sortUndefined: "last",
        cell: info => (
          <WatchlistDataTableSmartGridCell
            isLoading={column?.rows?.[info.row.original.entityInfo.key]?.isLoading ?? false}
            isError={column?.rows?.[info.row.original.entityInfo.key]?.isError ?? false}
            value={column?.rows?.[info.row.original.entityInfo.key]?.value}
            chunkMap={column?.chunkMap ?? {}}
            externalMap={column?.externalMap ?? {}}
            entityName={info.row.original.entityInfo.name}
            onClick={() => {
              const val = column?.rows?.[info.row.original.entityInfo.key]?.value?.replace(
                INLINE_ATTRIBUTION_REGEX_FOR_REMOVAL,
                "",
              )
              info.row.original.onChatWithCell?.({
                prompt: `Elaborate on: \n${val}`,
                value: undefined,
                entityInfo: info.row.original.entityInfo,
                columnName: `SmartGrid: ${column.title}`,
              })
            }}
          />
        ),
        meta: {
          menuLabel: column.title,
        },
      }
      return ret
    })
  }, [dataTab, setSelectedColumn, smartGridColumns])

  // Custom columns logic
  const { customColumns } = useCustomColumns()
  const { customColumnsModal } = useModal()

  const dynamicCustomColumns = useMemo(() => {
    if (dataTab !== "custom") return []

    // If no custom columns are selected, show a fake empty column
    if (customColumns.length === 0) {
      return [
        {
          id: "fake-empty-column",
          accessorFn: () => "",
          header: () => (
            <WatchlistDataTableFakeColumnHeaderCell
              onAddColumn={() => customColumnsModal.handleOpen()}
            />
          ),
          cell: () => "-",
          size: 350,
          enableHiding: false,
          enableSorting: false,
          meta: {
            menuLabel: "Add Column",
          },
        } as ColumnDef<WatchlistDataTabsTableEntity>,
      ]
    }

    return customColumns
      .map(customCol => {
        try {
          // Get the source table
          const sourceTable = watchlistDataTables[customCol.sourceTab as FinanceDataTabs]
          if (!sourceTable) return null

          // Find the matching column definition in the source table
          const sourceColumn = sourceTable.columns.find(
            (colDef: ColumnDef<WatchlistDataTabsTableEntity>) => {
              if (colDef.meta?.endpoint !== customCol.endpoint) return false

              const columnKeys = colDef.meta?.columnKeys
              if (!columnKeys) return false

              // Match using the primary key (first key in the array)
              const primaryKey = columnKeys[0]?.toString()
              return primaryKey === customCol.columnKey
            },
          )

          if (sourceColumn) {
            // Create a custom column definition based on the source column
            const customColumnDef: ColumnDef<WatchlistDataTabsTableEntity> = {
              ...sourceColumn,
              id: `custom-column-${customCol.endpoint}-${customCol.columnKey}`,
              meta: {
                ...sourceColumn.meta,
                menuLabel: customCol.displayName,
              },
            }
            return customColumnDef
          }
        } catch (error) {
          // Skip failed custom column creation
        }
        return null
      })
      .filter((col): col is ColumnDef<WatchlistDataTabsTableEntity> => col !== null)
  }, [dataTab, customColumns, customColumnsModal])

  const memo = useMemo(() => {
    const cols = columns.slice(0)
    cols.splice(2, 0, ...smartColumns, ...dynamicCustomColumns)
    return cols
  }, [columns, smartColumns, dynamicCustomColumns])

  const table = useReactTable({
    data: tableData,
    columns: memo,
    getCoreRowModel: getCoreRowModel(),
    getFilteredRowModel: getFilteredRowModel(),
    getPaginationRowModel: getPaginationRowModel(),
    getSortedRowModel: getSortedRowModel(),
    state: {
      sorting,
      pagination,
      columnVisibility,
      columnOrder,
    },
    onSortingChange: setSorting,
    onPaginationChange: setPagination,
    onColumnVisibilityChange: setColumnVisibility,
    onColumnOrderChange: setColumnOrder,
    enableSortingRemoval: false,
    enableSorting: !isFeatureBlocked,
    autoResetPageIndex: false,
  })
  const { pageSize, pageIndex } = table.getState().pagination
  const penultimateColumnId = table.getHeaderGroups()[0].headers.slice(-2)[0].id

  const downloadCSV = () =>
    downloadTableCSV({
      columns: table.getAllColumns().filter(col => col.id !== "actions"),
      entities,
      financialData,
      watchlistName,
      dataTab,
    })

  if (isLoading) return <WatchlistTableLoading />

  const orderedColumns = table.getAllColumns().sort((a, b) => {
    const aIndex = columnOrder.indexOf(a.id)
    const bIndex = columnOrder.indexOf(b.id)
    return aIndex === -1 ? 1 : bIndex === -1 ? -1 : aIndex - bIndex
  })

  return (
    <Box sx={{ position: "relative" }}>
      <WatchlistDataTabsTableHeaderMenu>
        {dataTab !== "smartGrid" && (
          <>
            <WatchlistDataTabsColumnsMenu
              selectAllColumns={() => {
                setColumnVisibility(
                  orderedColumns.reduce((acc, col) => {
                    if (col.getCanHide()) return { ...acc, [col.id]: true }
                    return acc
                  }, {}),
                )
              }}
              setColumnOrder={setColumnOrder}
              onReset={() => {
                setColumnOrder(defaultOrder)
                setColumnVisibility(
                  defaultHidden.reduce((acc, id) => ({ ...acc, [id]: false }), {}),
                )
              }}
              columns={orderedColumns}
              currentTab={dataTab}
            />
            <WatchlistDataTabsDownloadCsv
              disabled={
                isLoadingFinanceData || (dataTab === "custom" && customColumns.length === 0)
              }
              tooltip={
                isFeatureBlocked
                  ? "Upgrade to export to CSV"
                  : dataTab === "custom" && customColumns.length === 0
                  ? "Add columns to enable CSV export"
                  : undefined
              }
              onDownload={() => {
                watchlistCustomerIo.dataView.downloadButtonClicked(dataTab)
                if (isFeatureBlocked) {
                  planComparisonHandleOpen(WATCHLIST_FINANCIAL_DATA_FEATURE_NAME)
                } else {
                  downloadCSV()
                }
              }}
            />
            <WatchlistDataTabsAddColumnsTutorial
              onAddColumn={() => {
                // Trigger opening of the columns menu by programmatically clicking the button
                const addColumnsButton = document.getElementById(
                  WATCHLIST_DATA_TABS_ADD_COLUMNS_BUTTON_ID,
                )
                addColumnsButton?.click()
              }}
            />
          </>
        )}
        {dataTab === "smartGrid" && (
          <WatchlistSmartGridActions
            setSelectedColumn={setSelectedColumn}
            smartGridColumns={smartGridColumns}
            entities={entities}
            downloadCSV={downloadCSV}
            setSmartGridColumns={setSmartGridColumns}
          />
        )}
      </WatchlistDataTabsTableHeaderMenu>
      <Box
        onScroll={e => {
          if (e.currentTarget.scrollLeft > 0) {
            e.currentTarget.classList.add(FINANCE_TABLE_SCROLLED_HORIZONTALLY_CLASS)
          } else {
            e.currentTarget.classList.remove(FINANCE_TABLE_SCROLLED_HORIZONTALLY_CLASS)
          }
        }}
        sx={theme => ({ overflowX: "auto", ...theme.scrollbarStyles, mb: 8 })}
      >
        <WatchlistDataTabsTableContainer
          aria-label="Finance Table"
          showGradient={smartColumns.length > 0 || dataTab !== "smartGrid"}
          sx={{
            [`& .${FINANCE_TABLE_ROW_CLASS}`]: {
              gridTemplateColumns: `3px ${orderedColumns
                .filter(col => col.getIsVisible())
                .map(col => {
                  if (dataTab === "smartGrid" && col.id.startsWith("smart-grid-column-"))
                    return `minmax(${col.getSize()}px, 1fr)`
                  return col.id === "name" && (dataTab !== "smartGrid" || smartColumns.length === 0)
                    ? `minmax(${col.getSize()}px, 1fr)`
                    : `${col.getSize()}px`
                })
                .join(" ")}`,
            },
          }}
        >
          <Box className={FINANCE_TABLE_HEAD_CLASS}>
            {table.getHeaderGroups().map(headerGroup => (
              <Box className={FINANCE_TABLE_ROW_CLASS} key={headerGroup.id}>
                {headerGroup.headers.map(header => {
                  return (
                    <Box
                      className={FINANCE_TABLE_CELL_CLASS}
                      key={header.id}
                      sx={{
                        backgroundColor: "background.paper",
                        color: header.column.getIsSorted() ? "text.primary" : "text.secondary",
                        cursor: header.column.getCanSort() ? "pointer" : "default",
                        ...(header.id === penultimateColumnId ? { paddingRight: 0 } : {}),
                        ...stickyStyle(header.column.id),
                      }}
                      onClick={header.column.getToggleSortingHandler()}
                    >
                      <Box sx={{ display: "flex", alignItems: "center" }}>
                        {flexRender(header.column.columnDef.header, header.getContext())}
                        {{
                          asc: <ArrowUpward sx={{ ml: 0.5, fontSize: 12 }} />,
                          desc: (
                            <ArrowUpward
                              sx={{ ml: 0.5, fontSize: 12, transform: "rotate(180deg)" }}
                            />
                          ),
                        }[header.column.getIsSorted() as string] ?? null}
                      </Box>
                    </Box>
                  )
                })}
              </Box>
            ))}
          </Box>
          <Box className={FINANCE_TABLE_BODY_CLASS}>
            {table.getRowModel().rows.map(row => {
              return (
                <Box
                  className={
                    row.original.entityInfo.key === selectedEntityId
                      ? `${FINANCE_TABLE_ROW_CLASS} selected ${FINANCE_TABLE_ITEM_PREFIX}${row.original.id}`
                      : `${FINANCE_TABLE_ROW_CLASS} ${FINANCE_TABLE_ITEM_PREFIX}${row.original.id}`
                  }
                  onClick={() => {
                    onRowClick?.(row.original.entityInfo)
                  }}
                  key={row.id}
                >
                  {row.getVisibleCells().map(cell => {
                    return (
                      <Box
                        className={FINANCE_TABLE_CELL_CLASS}
                        key={cell.id}
                        sx={{
                          backgroundColor: "background.paper",
                          padding: 1,
                          ...(cell.column.id === penultimateColumnId ? { paddingRight: 0 } : {}),
                          ...(cell.column.getIndex() === 0 ? { paddingLeft: 0 } : {}),
                          ...stickyStyle(cell.column.id),
                        }}
                      >
                        {flexRender(cell.column.columnDef.cell, cell.getContext())}
                      </Box>
                    )
                  })}
                </Box>
              )
            })}
          </Box>
        </WatchlistDataTabsTableContainer>
        {tableData.length > pageSize && (
          <TablePagination
            sx={{ overflow: "hidden", position: "sticky", left: 0 }}
            rowsPerPageOptions={ROWS_PER_PAGINATION}
            component="div"
            count={table.getFilteredRowModel().rows.length}
            rowsPerPage={pageSize}
            page={pageIndex}
            slotProps={{
              select: {
                inputProps: { "aria-label": "rows per page" },
                native: true,
              },
            }}
            onPageChange={(_, page) => {
              table.setPageIndex(page)
            }}
            onRowsPerPageChange={e => {
              const size = e.target.value ? Number(e.target.value) : DEFAULT_PAGE_SIZE
              table.setPageSize(size)
            }}
            ActionsComponent={WatchlistTablePaginationActions}
          />
        )}
      </Box>
      {isFeatureBlocked && (
        <WatchlistDataTabsTableUnlockBanner
          onUpgrade={() => {
            watchlistCustomerIo.dataView.upgradeButtonClicked(dataTab)
            planComparisonHandleOpen(WATCHLIST_FINANCIAL_DATA_FEATURE_NAME)
          }}
          entitiesCount={allEntitiesCounts}
        />
      )}
    </Box>
  )
}

export { WatchlistDataTabsTable }
